<!-- 折线图 -->
<template>
  <div class="hline">
    <ve-line
      :data="chartData" :settings="chartSettings"
      :extend="chartExtend"   width="250px" height="250px"
    ></ve-line>
  </div>
</template>

<script>
export default{
  data(){
    this.chartSettings={
      dimension:['日期'],
      metrics:['访问用户','下单用户'],
      // 1.settings内scale作用是为了坐标刻度不会强制包含零刻度
      scale:[true,true]      //？？？
    }
    return{
      chartData: {
        columns: ['日期', '访问用户', '下单用户'],
        rows: [
          { '日期': '1/1', '访问用户': 1393, '下单用户': 1093 },
          { '日期': '1/2', '访问用户': 3530, '下单用户': 3230 },
          { '日期': '1/3', '访问用户': 2923, '下单用户': 2623 },
          { '日期': '1/4', '访问用户': 0, '下单用户': 1423 }
        ]
      },
      chartExtend: {
        // 2.extend内series的label作用是为了显示每个拐点数量；
        series: {
          label: {
            // normal: {
            //   show: true,
            //   color: "#fff000",
            //   fontSize: 18
            // }
          },
          // 3.extend内series的symbolSize作用是为了设置每个拐点小圆点的大小；
          symbolSize: 10,
          // 4.extend内series的color作用是为了设置折线的颜色；
          // color: ['#44F0FF']
        },
        // 5.extend内legend的作用是为了隐藏展现不同系列的标记；
        legend: {
          show: false
        },
        // 6.extend内grid的作用是为了控制y轴密集程度；
        grid: {
          y: 10,
          y2: 50,
          left: 10,
          containLabel: true
        },
        // 7.extend内xAxis的作用是为了设置x轴字体、轴线样式；
        xAxis: {
          axisLabel: {
            textStyle: {
              color: '#fff',
              fontSize: 14
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          }
        },
        // 8.extend内yAxis的作用是为了设置y轴字体、轴线样式；
        yAxis: {
          axisLabel: {
            textStyle: {
              color: '#fff',
              fontSize: 14
            }
          },
          splitLine: {
            show: false
          },
          position: 'left',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          }
        }
      }

    }
  }
}
</script>

