<!-- 柱状图 -->
<template>
  <div>
    <h3 v-text="'活跃门店走势图'"></h3>
    <ve-histogram
      :data="chartData"  :extend="chartExtend"
    ></ve-histogram>
  </div>
</template>

<script>
  // 1.extend内legend作用是为了隐藏展现不同系列的标记；
  // 2.extend内grid的作用是为了控制x、y轴密集程度；
  // 3.extend内xAxis的作用是为了设置x轴字体、轴线样式；
  // 4.extend内yAxis的作用是为了设置y轴字体、轴线样式；
  // 5.extend内series的作用是为了柱状条样式。
export default{
  data(){
    return{
      chartExtend:{
        legend:{
          show: false
        },
        grid:{
          left:'5%',
          containLabel:true,
          top:'5%',
          bottom:'5%'
        },
        xAxis: {
          axisLabel: {
            textStyle: {
              color: '#fff',
              fontSize: 14
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          }
        },
        yAxis: {
          axisLabel: {
            textStyle: {
              color: '#fff',
              fontSize: 14
            }
          },
          splitLine: {
            show: false
          },
          position: 'left',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          }
        },
        series: {
          type: 'bar',
          itemStyle: {
            normal: {
              color: 'rgba(68,240,255,1)'
            }
          }
        }


      },
     chartData: {
       columns: ['日期', '访问用户'],
       rows: [
         { '日期': '1/1', '访问用户': 1393 },
         { '日期': '1/3', '访问用户': 3530 },
         { '日期': '1/5', '访问用户': 2923 },
         { '日期': '1/7', '访问用户': 120}
       ]
     }
    }
  }
}
</script>

<style scoped lang="less">
h3{
  color:#fff;  font-weight: normal;
}
</style>
