<!-- 产品分类统计 -->
<template>
  <div class="classify-ring">
    <ve-ring
      :data="chartData" width="260px" height="260px"
      :extend="chartExtend"     :settings="chartSettings"
    ></ve-ring>
  </div>
</template>

<script>
export default{
  data(){
    this.chartSettings={
      dimension:"name",
      metrics:"value",
      radius:['35','50'],
      offsetY: 80,
       labelLine: {
        normal: {
          lineStyle: {
            color: '#fff'
          }
        }
      },
      label: {
        normal: {
          formatter: '{b}: {d}%',
          padding: [0, -10]
        }
      }

    }
    return{
       chartExtend:{
          legend:{
            show:false
          }
       },
       chartData:{
         columns: ['name', 'value'],
         rows: [
           { 'name': '活跃', 'value': 10},
           { 'name': '不活跃', 'value': 90},
         ]
       }
    }

  }
}
</script>

