<template>
  <div class="h-ring">
    <div class="ring-relative">
      <ve-ring  :data="chartData" :settings="chartSettings"
      width="112px" height="112px" :extend="chartExtend"
      :tooltip="tooltip"
      ></ve-ring>
      <span class="desc">{{percentage}}</span>
    </div>
    <div class="title">{{text}}</div>
  </div>
</template>

<script>
  import 'v-charts/lib/style.css'
  export default {
    data () {
      // 1.settings内label作用是为了隐藏视觉引导线
      // 2.settings内radius作用是为了设置环图外半径与内半径；
      // 3.settings内offsetY作用是为了设置纵向偏移量使环图居中；
      // 4.tooltip是为了设置鼠标hover时不触发提示框；
      // 5.extend内legend作用是为了隐藏展现不同系列的标记；
      // 6.extend内color作用是为了设置环图颜色；
      // 7.props内text环图底部文字描述；
      // 8.props内percentage表示环图中心百分比。
      this.chartSettings={
        dimension:['name'],
        metrics:['value'],
        label: {
          normal: {
            show: false
          }
        },
        radius: ['25', '40'],
        offsetY: 56
      }
     return {
        chartData: {
          columns: ['name', 'value'],
          rows: [
            { 'name': '活跃', 'value': 10},
            { 'name': '不活跃', 'value': 90},
          ]
        },
        // 小提示条
        tooltip: {
           trigger: 'none'
        },
        chartExtend: {
          legend: {
            show: false
          },
          color: ['#44F0FF', '#19355A']
        }
      }
    },
    props:{
      percentage:{
        type:String,
        required:true
      },
      text:{
        type:String,
        required:true
      }
    }
  }
</script>

<style scoped lang="less">
.h-ring{
  color: #D1DBE5;
  .ring-relative{
    position: relative;
    >span{
      position: absolute; left: 50%; top: 50%;  transform: translate(-50%,-50%);
    }
  }
  .title{
    text-align: center;
  }
}
</style>
